
/*about outer-----------------------------------------------*/
.about-outer-box{
	padding: 0vw 0vw 4vw 4vw;
}

/*about paragraph--------------------------------------------*/
.about-img-box{
	background-image: url('../img/myPhoto2.png');
	background-image: url('../img/myPhoto3.png');
	background-repeat: no-repeat;
/*	background-size: contain;*/
	background-size: 25vw 30vw;
	background-position: left center;
	height: 30vw;
	position: relative;
}
.about-img-box::before{
	content: "";
	position: absolute;
    height: 20vw;
    width: 19vw;
    background-image: url('../img/sArrow2.png');
    top: -2vw;
    left: 10vw;
    background-size: 13vw 10vw;
    background-repeat: no-repeat;
    transform: rotateY(180deg);

}
.about-img-box p{
	background-color: #5e17eb;
	padding: 1.5vw;
	color: white;
	font-size: 1.5vw;
	text-align: justify;
	border-radius: 2vw;
	font-family: 'Poppins',sans-serif;
	position: absolute;
	right: 2vw;
	top: 2vw;
	width: 58vw;
	border: .5vw solid white;
	box-shadow: rgba(0, 0, 0, .5) 0px 0px 0.25em, rgba(0, 0, 0, .5) 0px 0.25em 1em;
	box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.about-img-box p:hover{
	animation: none;
}
.about-img-box p span{
	color: #c78f7b;
	color: black;
	text-shadow: rgba(255, 255, 255, .2) 0px 0px 0.25em, rgba(255, 255, 255, .2) 0px 0.25em 1em;
	font-size: 1.8vw;
	font-weight:bold;

}

/*personal-----------------------------------------------------------------*/
.about-heading-common{
	font-size: 3vw;
	font-weight: bold;
	font-family: 'Poppins',sans-serif;
	margin: 3vw 0vw .5vw 0vw;
	padding: 1vw 0vw 0vw 1vw;
	position: relative;
}
.about-heading-common::after{
	content: "";
	position: absolute;
	right: 0vw;
	top: 0vw;
	width: 100%;
	height: .2vw;
	background-color:#5e17eb ;
}
.about-heading-common::before{
	content: "";
	position: absolute;
	left: 0vw;
	top: 0vw;
	height: 100%;
	width: .2vw;
	background-color:#5e17eb ;
}
.about-personal-item{
	font-size: 2vw;
	color: black;
	font-family: 'Poppins',sans-serif;
	font-weight: 500;
	margin: .5vw 0vw;
}
.about-personal-item span{
	font-size: 1.7vw;
	margin-left: 1vw;
	color: #5e17eb;

}

/*interest--------------------------------------------------------------------*/
.about-interest-item{
	color: #5e17eb;
	font-size: 1.7vw;
	position: relative;
	font-family: 'Poppins',sans-serif;
	font-weight: 500;
	list-style:none;
	margin: .5vw 0vw .5vw 0vw;
	padding:0vw 0vw 0vw 1vw;
}

.about-interest-item::before{
	content: "";
	position: absolute;
	left: 0vw;
	top: 0vw;
	height: 100%;
	width: .3vw;
	background-color: #5e17eb;
}



/*media query*********************************************************************/
/*extra small*/
@media screen and (max-width:576px){
/*about outer-----------------------------------------------*/
	.about-outer-box{
		padding: 0vw 0vw 0vw 4vw;
	}
	.about-img-box {
    background-size: 100% 100%;
    background-position: left top;
    height: 147vw;
	}
	.about-img-box::before {
    height: 30vw;
    width: 20vw;
    top: 65vw;
    left: 60vw;
    background-size: 20vw 25vw;
	}
	.about-img-box p{
	padding: 1.5vw;
    font-size: 3vw;
    left: -3vw;
    top: 83vw;
    width: 108%;
	}

	.about-img-box p span{
		font-size: 3.4vw;
	}

	/*personal-----------------------------------------------------------------*/
	.about-heading-common{
		font-size: 7vw;
		margin: 6vw 0vw .5vw 0vw;
		padding: 2vw 0vw 0vw 2vw;
	}
	.about-personal-item{
		font-size: 5vw;
		margin: 1vw 0vw;
	}
	.about-personal-item span{
		font-size: 4.7vw;
	}

	/*interest--------------------------------------------------------------------*/
	.about-interest-item{
		margin: 1vw 0vw 1vw 0vw;
		font-size: 4vw;
		padding:0vw 0vw 0vw 2vw;
	}
	.about-interest-item::before{
	width: .5vw;
	}
}


